<template>
  <div class="app">
    <div class="calculator">
      <div class="board">{{current|| '0'}}</div>
      <div class="special C" @click="clear()">C</div>
      <div class="special %" @click="append('%')">%</div>
      <div class="special sign">+/-</div>
      <div class="operator /" @click="append('/')">/</div>
      <div class="num 7" @click="append(7)">7</div>
      <div class="num 8"  @click="append(8)">8</div>
      <div class="num 9"  @click="append(9)">9</div>
      <div class="operator X" @click="append('x')">X</div>
      <div class="num 4"  @click="append(4)">4</div>
      <div class="num 5"  @click="append(5)">5</div>
      <div class="num 6"  @click="append(6)">6</div>
      <div class="operator -" @click="append('-')">-</div>
      <div class="num 1"  @click="append(1)">1</div>
      <div class="num 2"  @click="append(2)">2</div>
      <div class="num 3"  @click="append(3)">3</div>
      <div class="operator +" @click="append('+')">+</div>
      <div class="num 0" @click="append(0)">0</div>
      <div class="dot" @click="dot()">.</div>
      <div class="equals" @click="operation_check()">=</div>

    </div>
  </div>
</template>

<script>
export default {
  name: 'calculator',
  data () {
    return {
      current: ''
    }
  },
  methods: {
    clear () {
      this.current = ''
    },
    append (num) {
      this.current = `${this.current}${num}`
    },
    add () {
      this.append('+')
    },
    operation_check () {
      var rePlus = /\++/
      var reMinus = /-+/
      var reMultiply = /x+/
      var reDivide = /\/+/
      if (this.current.search(rePlus) !== -1) {
        this.equals_add()
      }
      if (this.current.search(reMinus) !== -1) {
        this.equals_minus()
      }
      if (this.current.search(reMultiply) !== -1) {
        this.equals_multiply()
      }
      if (this.current.search(reDivide) !== -1) {
        this.equals_divide()
      }
    },
    equals_add () {
      var sum = 0
      var array = this.current.split('+')
      console.log(array)
      for (var n = 0; n < array.length; n++) {
        sum += parseFloat(array[n])
      }
      console.log(sum)
      this.current = sum.toString()
    },
    equals_minus () {
      var array = this.current.split('-')
      console.log(array)
      var sub = array[array.length - 1]
      for (var n = array.length - 2; n >= 0; n--) {
        sub -= array[n]
      }
      this.current = -sub.toString()
    },
    equals_multiply () {
      var array = this.current.split('x')
      var pro = 1

      for (var n = 0; n < array.length; n++) {
        pro *= array[n]
      }
      this.current = pro.toString()
    },
    equals_divide () {
      var array = this.current.split('/')
      var rem = array[0]
      for (var n = 1; n <= array.length - 1; n++) {
        rem = rem / array[n]
      }
      this.current = rem.toString()
    },
    dot () {
      if (this.current[this.current.length - 1] !== '.') {
        this.append('.')
      }
    }
  }
}
</script>

